@import url('https://fonts.googleapis.com/css2?family=Inter+Tight&family=PT+Sans&display=swap');


:root {
    --color-primary: #c0c0c0e5;
    --color-secondary: #fff8f2;
    --color-text: #000000;
    --font-primary: 'PT Sans', sans-serif;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 16px;
    line-height: 1;
}

body {
    background: linear-gradient(190deg,
            #edd7bf 100%,
            var(--color-secondary) 0%);
    color: var(--color-text);
    font-family: var(--font-primary);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color: #222;
    overflow: auto;
}

.Login {
  color: #fff8f2;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 0 0 5px #1e3f5a, 3px -1px 5px #271c6c
}

.Login h1 {
    display: flex;
    align-self: center;
    text-align: center;
    margin-top: 2vh;
    margin-bottom: 1vh;
    color: #fff8f2;
    text-shadow: 0 0 5px #1e3f5a, 3px -1px 5px #271c6c
}

.Login label {
  font-weight: 600;
  font-size: 1.5rem;
}

.Login input {
  min-width: 300px;
  display: block;
  padding: 1rem;
  border: none;
  border-radius: 4px;
  margin-bottom: 0.5rem;
  margin-top: 5px;
}

.Login button {
  align-items: center;
  flex-direction: column;
  border-radius: 15px;
  background-color: #ffffffb6;
  margin: 0.2rem 0 0 0;
  min-width: 300px;
  font-size: 1.6rem;
  font-weight: 600;
  padding: 1rem;
  background-color: #444444;
  /* green */
  color: #fff;
  /* white */
  text-shadow: 0 0 5px #1e3f5a, 3px -1px 5px #271c6c;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
}

.Login .error {
  color: #cc0000;
  font-weight: 600;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}
